import { useLocation } from "react-router-dom";
import { useStudentOneProfile } from "../../Hooks/ApiHooks/StudentProfile/student-manager-api";
import NavbarConfig from "../../Shared/Navbar/NavbarConfig";
import GoBack from "../../Shared/StaffStudentProfile/GoBack";
import StaffStudentProfileContainer from "../../Shared/StaffStudentProfile/StaffStudentProfileContainer";
import StudentProfilePhotoHeader from "../../Shared/StaffStudentProfile/StudentProfilePhotoHeader";
import StudentProfileHeader from "../../Shared/StaffStudentProfile/StudentProfileHeader";
import BorderBottom from "../../Ui/Border/BorderBottom";
import StudentAdmissionFees from "./StudentAdmissionFees";
import { useTranslation } from "react-i18next";
import StudentAllTotalAdmissionFees from "./StudentAllTotalAdmissionFees";

const StudentAdmissionFeesProfile = () => {
  const { t } = useTranslation();
  const getQuery = useLocation();

  const { studentOneProfile } = useStudentOneProfile({
    sid: getQuery.state?.sid,
    skip: !getQuery.state?.sid,
  });

  // console.info("studentOneProfile", studentOneProfile);
  return (
    <>
      <NavbarConfig isView={getQuery.state?.viewAs}>
        <GoBack />
        <StaffStudentProfileContainer>
          <StudentProfileHeader
            title={t("student_admission_fees")}
          ></StudentProfileHeader>
          <BorderBottom />
          <StudentProfilePhotoHeader
            name={`${studentOneProfile?.studentFirstName ?? ""} ${
              studentOneProfile?.studentMiddleName
                ? `${studentOneProfile?.studentMiddleName} `
                : ""
            }${studentOneProfile?.studentLastName ?? ""}`}
            grnumber={studentOneProfile?.studentGRNO ?? ""}
            rollnumber={studentOneProfile?.studentROLLNO ?? ""}
            activeClass={studentOneProfile?.studentClass?.classTitle ?? ""}
            profilePhoto={studentOneProfile?.studentProfilePhoto ?? ""}
          >
            <StudentAllTotalAdmissionFees />
          </StudentProfilePhotoHeader>
          <BorderBottom />
          <StudentAdmissionFees />
        </StaffStudentProfileContainer>
      </NavbarConfig>
    </>
  );
};

export default StudentAdmissionFeesProfile;
